@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400&display=swap);
/*import google font*/


/*reset*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Montserrat", sans-serif;
  background-image: url("https://www.pizzahut.com.sv/static/media/menu/webp/background-mx.webp" );
  color:#1b1b1b;
  line-height: 2rem;
}

h1,h2{
  padding-bottom: 2rem;
}


/*clase general para contenedores*/

.flex-container {
  max-width: 85%;
  margin: 0 auto;
}

.sections-container{
  padding: 4rem 0;
}


/*clase general para contenedores - responsive*/

@media (min-width: 240px) and (max-width: 880px) {
  .flex-container {
    max-width: 90%;
  }
}

header {
  width: 100%;
  background: #b42323;
  padding: 0.5rem 0;
}

.nav_bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .bar-btn,
#menu {
  display: none;
}
.nav_bar_list li {
  display: inline-block;
  list-style: none;
  padding: 0 0.8rem;
}
.nav_bar_list .link {
  color: #fff;
  text-decoration: none;
}
.nav_bar_list .link:hover {
  text-decoration: underline;
  transition: all 1000ms ease-in-out;
}
.nav_bar_logo {
  width: 4%;
}

/*responsiver NavBar*/

@media (min-width: 240px) and (max-width: 880px) {
  .nav_bar .bar-btn {
    font-size: 2.5rem;
    color: #fff;
    display: flex;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }

  .nav_bar_list {
    display: none;
    top: 4.6rem;
    right: 0;
    position: absolute;
  }
  .nav_bar_list li {
    padding: 1.3rem 0.8rem;
  }

  .nav_bar_logo {
    width: 8%;
  }
  .nav_bar #menu:checked ~ .nav_bar_list {
    background:#b42323;
    width: 100%;
    height: 60vh;
    display: flex;
    flex-direction: column;
    text-align: justify;
    justify-content: center;
  }
}

.about-container{
    text-align: justify;
    background-image: url(data:image/png;base64,);
}
.button__submit{
    width:100%;
    padding:0.5rem  1rem;
    cursor: pointer;
    background: #D6D9DA;
    border:solid #00000049;
    color:#788D96;
    border-radius:15px;
    font-size:1.2rem;
}
.button__danger{
    background: #e74c3c;
    width:100%;
    padding:0.5rem  1rem;
    cursor: pointer;
    border:solid #00000049;
    color:#eeeeee;
    border-radius:5px;
    font-size:1.2rem;
    margin-top: 1rem;
}
.contact-container{
    display: flex;
    justify-content: space-between;
}
.contact-container img{
    width: 50%;
}

.form-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: start;
}
.form-container input, textarea{
    width: 100%;
    margin-bottom: 1rem;
    padding:1rem;
    border-radius: 5px;
    border:1px solid #000000a1;
}

@media (min-width:240px) and (max-width: 880px){

    .contact-container{
        flex-direction: column-reverse;
        padding: 3rem 0;
    }
    .contact-container img{
        width: 100%;
    }


}
.hero_container {
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url(/static/media/pupusas_2.223cf2c0.jpg);
  background-position: center;
  background-size: cover;
}
.hero_container img {
  width: 100%;
}
.hero_container > .titles > span {
  font-size: 2.5rem;
}
.hero_container > .titles > p {
  font-size: 1.5rem;
  padding: 2rem;
  
}
.titles{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  width: 100%;
  height: 100%;
  background:rgba(0, 0, 0, 0.575);
  color:#E8E8E8;
}
/*responsive*/

@media (min-width: 240px) and (max-width: 880px) {
  .hero_container > .titles {
    text-align: center;
  }
  .hero_container > .titles > span {
    font-size: 2rem;
  }
  .hero_container > .titles > p {
    font-size: 1.2rem;
    padding: 2rem;
  }
}

.food-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
  border-radius: 2em;
}

/*responsive*/

@media (min-width:240px) and (max-width:880px){
    .food-container{
        grid-template-columns: repeat(1,1fr);
    }
}

.card{
    box-shadow: 1px 2px 20px #0000001e;
    padding: 1rem;
    border-radius: 7px;
}
.card ul li{
    list-style: none;
}

.card img{
    width: 380px;
    height: 326px;
}
.card-article ul li{
    list-style: none;
}

.card-article .imgarticle{
    margin: 0 auto;
    width: 80%;
}
.card-article .imgarticle img{
    width: 100%;
}

/*responsive*/

@media (min-width:240px) and (max-width:880px){
    .card-article .imgarticle{
        width: 100%;
    }
}
.reservation-container{
    display: flex;
    align-items: center;
}
.reservation-container> img{
    width: 50%;
    height: 80%;
}

.reservation-form{
    width: 100%;
    display: flex;
    flex-direction: column;
}

.reservation-form> input{
    padding: 1rem;
    margin: 0.3rem 0;
    border-radius: 6px;
    border:0.3em solid #000000a1;
    font-size: 1.2rem;
}

/*responsive*/

@media (min-width:240px) and (max-width:880px){
    .reservation-container{
        flex-direction: column-reverse;
    }
    .reservation-container>img{
        width: 100%;
    }
}

footer{
    background: #030303;
    color: #CCCCCC;
}
a {
    color: #CCCCCC;
}
footer ul li{
    list-style: none;
}


